import React, { Component } from 'react';

class TimelineDetails extends Component {
  constructor(props) {
    super(props);

    if( props.details.images ) {
      this.images = props.details.images.map((image) => {
        return require(`../../images/timeline/${image}`);
      });
    }

    this.state = {
      mainImage: props.details.images ? this.images[0] : null
    }

    this.handleImageClick = this.handleImageClick.bind(this);
  }

  handleImageClick(el) {
    this.setState({
      mainImage: el.target.src
    });
  }

  renderThumbnails() {
    if(!this.props.details.images) {
      return null;
    }

    // Don't add the thumbnails if there's only one image
    if(this.images.length < 2) {
      return null;
    }

    return this.images.map((i,k) => {
      return (
        <div key={k} className="TimelineDetails__Thumb">
          <img src={i} alt={`${k}`} onClick={this.handleImageClick}/>
        </div>
      );
    });
  }

  renderDetailsText() {
    return this.props.details.text.map((t,k) => {
      return(
        <p key={k}>{t}</p>
      );
    });
  }

  render () {
    return(
      <div className="ReactModal__Details TimelineDetails__Content">
        {this.props.details.images ?
        <div className="TimelineDetails__Images">
          <div className="TimelineDetails__Images--main">
            <img alt="Main" src={this.state.mainImage}/>
          </div>
          <div className="TimelineDetails__Images--more">
            { this.renderThumbnails() }
          </div>
        </div>
        : null }
        <div className="TimelineDetails__Text">
          {this.renderDetailsText()}
        </div>
      </div>
    )
  }
}

export default TimelineDetails;



